import myEditor from "@/components/other/robin-editor/editor.vue"
// 滑动单元格
import uniSwipeAction from '@/components/uni-ui/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-ui/uni-swipe-action-item/uni-swipe-action-item.vue'
import dynamicTags from '@/components/other/dynamic-tags/dynamic-tags.vue'
import kGameDetail from '@/components/k-ui/k-game-detail/k-game-detail.vue'
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"
export default {
	components: {
		myEditor,
		uniSwipeAction,
		uniSwipeActionItem,
		dynamicTags,
		kGameDetail,
		uniNumberBox
	},
	data() {
		return {
			name: '',
			source: '',
			url: '',
			registerUrl: '',
			contact: '',
			date1: '',
			date2: '',
			date3: '',
			date4: '',
			gameContent: '',
			// 活动级别
			gameType: '',
			gameTypeList: ['院级', '校级', '市级', '省级', '国家级', '国际级','其他'],
			popupTypeFlag: false,
			// 活动类别
			category: '',
			categories: [],
			categoryIds: [],
			popupKindFlag: false,
			// 步骤条
			stepActive: 0,
			stepList: [{
				cuIcon: 'usefullfill',
				name: '基本信息'
			}, {
				cuIcon: 'radioboxfill',
				name: '竞赛描述'
			}, {
				cuIcon: 'radioboxfill',
				name: '上传附件'
			}, {
				cuIcon: 'roundcheckfill',
				name: '确认发布'
			}],
			// 比赛方式
			gameWayRadio: '1',
			num: 1,
			// 队伍规模
			membersShowFlag: false,
			// 上传图片	{url: 'https://img.yzcdn.cn/vant/leaf.jpg', name: '图片1'}
			picList: [],
			// 滑动单元格
			options: [{
				text: '删除',
				style: {
					backgroundColor: '#dd524d'
				}
			}],
			// 活动标签
			tagList: [],
			enableDel: true,
			enableAdd: true,
			// 时间选择器开始 解释时间
			startTime: '',
			endTime: '',
			// 预览的数据
			games: [],
			poster:[],
			// 编辑
			curGameId: '',
			user: '',
			editFlag: false

		}
	},
	mounted() {
	    this.user = this.$store.getters.getUserInfo
	},
	onLoad(options) {		
		if(options.id!=undefined){
			console.log(options.id)
			this.curGameId = options.id
			this.editFlag = true
			this.loadData()
		}
		this.getCategories()
		this.getStartDate()
		this.getEndDate()
	},
	methods: {
		saveEditor(e){
			// console.log(e)
			this.gameContent = e.htmlData
			// console.log(this.gameContent)
		},
		resetEditor() {
			this.gameContent = ""
		},
		Date1Change(e){
			this.date1 = e.detail.value
		},
		Date2Change(e){
			this.date2 = e.detail.value
		},
		Date3Change(e){
			this.date3 = e.detail.value
		},
		Date4Change(e){
			this.date4 = e.detail.value
		},
		test(){
			// this.$common.isUnknowTime(this.date1)
			// console.log("this.name: " + this.name)
			// console.log("this.source: " + this.source)
			// console.log("this.url: " + this.url)
			// console.log("this.gameType: " + this.gameType)
			console.log("this.category: " + JSON.stringify(this.category))
			console.log(this.categories)
			// console.log("this.teamSize: " + this.num)
			// console.log(this.date1)
			// console.log(this.date2)
			// console.log(this.date3)
			// console.log(this.date4)
			// console.log("this.gameContent: " + this.gameContent)
			// console.log("this.picList: "  + this.picList)
			// console.log(this.tagList)	
		},
		async loadData() {
			const res = await this.$api.request({
				url: '/game/' + this.curGameId,
			})
			console.log(res)
			if(res.status == 200){
				const _this = this
				const game = res.data.game
				this.name = game.gameName
				this.source = game.gameSource
				this.url = game.gameUrl
				this.registerUrl = game.registerUrl
				this.gameType = game.gameType
				this.category = game.gameCategory
				if(game.teamSize>1){
					this.gameWayRadio = '2'
					this.membersShowFlag = true
				}
				this.num = game.teamSize
				this.date1 = game.registerStartTime
				this.date2 = game.registerEndTime
				this.date3 = game.gameStartTime
				this.date4 = game.gameEndTime
				this.gameContent = game.gameContent
				this.contact = game.contact
				game.gameTags.forEach(function(r){
					_this.tagList.push(r.tagName)
				})
				this.picList.push(game.posterUrl)
				// console.log(game.posterUrl)
				// this.source = game.
			} else {
				this.$api.msg("服务器正忙~")
			}	
		},
		getStartDate(){
			// const now = new Date()
			// const now_year = now.getFullYear()-2
			// const a = now_year + '-01-01'
			// this.startTime = a
			this.startTime = '2018-01-01'
		},
		getEndDate(){
			const now = new Date()
			const now_year = now.getFullYear()+2
			const a = now_year + '-01-01'
			// console.log(a)
			this.endTime = a
			// return now_year + '-01-01'
		},
		// 比赛类型
		chooseType(event) {
			const {value,index} = event.detail;
			this.gameType = index+1
			this.popupTypeFlag = false;
		},
		closeTypePopup() {
			this.popupTypeFlag = false;
		},
		// 比赛类别
		chooseKind(event) {
			// const {value,index} = event.detail;
			// this.category = index+1
			// this.popupKindFlag = false;
			
			// console.log(event)
			const {value,index} = event.detail;
			this.category = {
				id: this.categoryIds[index],
				category: value
			}
			console.log(this.category)
			this.popupKindFlag = false;
		},
		closeKindPopup() {
			this.popupKindFlag = false;
		},
		onChangeGameWay(e) {
			this.gameWayRadio = String(e.detail)
			if (this.gameWayRadio == 2) {
				this.membersShowFlag = true
			} else {
				this.membersShowFlag = false
				this.num=1
			}
		},
		change(e) {
			this.num = e
			// console.log(this.num)
		},
		nextStep() {
			// console.log(this.date3)
			if(this.stepActive == 0) {
				if(this.name == "")	{
					this.$api.msg("请填写竞赛名称~") 
					return
				}
				if(this.source == "") {
					this.$api.msg("请填写主办方~") 
					return
				}
				if(this.gameType == "")	{
					this.$api.msg("请选择竞赛级别~") 
					return
				}
				if(this.category == "")	{
					this.$api.msg("请选择竞赛类别~")
					return
				}
				if(this.date1 == "") {
					this.$api.msg("请选择报名开始时间~") 
					return
				}
				if(this.date2 == "") {
					this.$api.msg("请选择报名结束时间~") 
					return
				}
				if(this.date3 == ""){
					this.$api.msg("请选择竞赛开始时间~") 
					return
				}
				if(this.date4 == "") {
					this.$api.msg("请选择竞赛结束时间~") 
					return
				}
			}
			if(this.stepActive == 1){
				if(this.gameContent == ""){
					this.$api.msg("请填写竞赛内容并保存~")
					return
				}
			}
			this.stepActive = this.stepActive >= 4 ? this.stepActive : this.stepActive + 1
			// console.log(this.stepActive)
		},
		postGame() {
			if(this.picList.length == 0){
				this.$api.msg("请上传竞赛海报~")
				return
			}
			const _this = this
			if(this.tagList.length == 0){
				uni.showModal({
					title: '提示',
					content: '确定不添加竞赛标签吗？',
					showCancel: true,
					confirmText: '确定',
					success: (e) => {
						if (e.confirm) {
							uni.showLoading({
								title:'发布中...'
							})
							setTimeout(()=>{
								if(_this.curGameId==""){
									_this.post('/game/add')
								}else {
									_this.post('/game/update')
								}
							}, 1000)
						}
					}
				})
			}else {
				uni.showLoading({
					title:'发布中...'
				})
				setTimeout(()=>{
					if(_this.curGameId==""){
						_this.post('/game/add')
					}else {
						_this.post('/game/update')
					}
				}, 1000)
			}

		},
		finsh() {
			uni.navigateBack()
		},
		deleteGame() {
			if(this.user.userType>=3) {
				uni.showModal({
					title: '提示',
					content: '权限不足，无法删除~',
					showCancel: false,
					confirmText: '返回',
					success: (e) => {
					}
				})
				return
			}
			const _this = this
			uni.showModal({
				title: '删除提示',
				content: '删除不可恢复，确定删除吗？',
				showCancel: true,
				confirmText: '删除',
				success: (e) => {
					if (e.confirm) {
						uni.showLoading({
							title:'删除中...'
						})
						_this.delete()
					}
				}
			})	
		},
		async delete(){
			const res = await this.$api.request({
				url: '/game/delete/' + this.curGameId
			})
			uni.hideLoading()
			if(res.status == 200){
				this.$api.msg("删除成功")
				uni.navigateBack()
			} else if(res.status == 2001 || res.status == 2002){
				this.$api.msg(res.msg)
			} else {
				this.$api.msg("系统繁忙")
			}	
		},
		editGame(){
			if(this.user.userType>=3) {
				uni.showModal({
					title: '提示',
					content: '权限不足,无法编辑~',
					showCancel: false,
					confirmText: '返回',
					success: (e) => {
					}
				})
				return
			}
			const _this = this
			uni.showModal({
				title: '编辑提示',
				content: '您确定要重新编辑吗？',
				showCancel: true,
				confirmText: '确定',
				success: (e) => {
					if (e.confirm) {
						// uni.navigateTo({
						// 	url: '/pages/user/admin/post-game?id=' + _this.currGameId
						// })
						this.editFlag = true
						this.stepActive=0
					}
				}
			})
		},
		async post(url) {
			// this.test()
			const _this = this
			const res = await this.$api.request({
				url: url,
				contentType: "application/json",
				method: 'POST',
				data: {
					game: {
						id: this.curGameId,
						gameName: this.name,
						posterUrl: this.picList[0],
						gameContent: this.gameContent,
						contact: this.contact,
						registerStartTime: this.date1,
						registerEndTime: this.date2,
						gameStartTime: this.date3,
						gameEndTime: this.date4,
						gameSource: this.source,
						gameType: this.gameType,
						gameCategory: this.category,
						// gameCategory: {
						// 	'id': this.category,
						// 	'category': this.categories[this.category-1]
						// },
						gameUrl: this.url,
						registerUrl: this.registerUrl,
						teamSize: this.num,
					},
					tags: JSON.stringify(this.tagList)
				}
			})
			console.log(res)
			console.log(res.status)
			// console.log(this.tagList)
			if(res.status == 200){
				console.log("game发布成功")
				this.curGameId = res.data.id
				if(this.picList[0].startsWith("http://tmp") || this.picList[0].startsWith("wxfile:")){
					console.log("更新图片")
					this.upload()
				}else {
					uni.hideLoading()
					this.$api.msg("发布成功~")
					setTimeout(()=>{
						uni.showLoading({
							title:'预览中...'
						})
						this.getGame(this.curGameId)
					}, 1000)
				}

			}else {
				this.$api.msg("服务器正忙~")
			}
		},
		async getGame(id) {
			console.log(id)
			const res = await this.$api.request({
				url: '/game/' + id,
			})
			// console.log(res)
			if(res.status == 200){
				this.games = res.data.game
				this.poster[0] = res.data.poster
				setTimeout(()=>{
					this.stepActive = 3
					uni.hideLoading()
				}, 1000)
				// console.log(this.games)
				// console.log(this.poster)
			}		
		},
		lastStep() {
			this.stepActive = this.stepActive <= 4 ? this.stepActive - 1 : this.stepActive
			// console.log(this.stepActive)
		},
		afterReadPic(e) {
			const file = e.detail.file
			this.picList.push(file.path)
		},
		upload(){
			// console.log("上传文件")
			const _this = this
			console.log(this.picList)
			// console.log(this.curGameId)
			// console.log(this.picList)
			uni.uploadFile({
			  url: this.$config.def().baseUrl + '/upload/upload/' + this.curGameId, 
			  filePath: _this.picList[0],
			  name: 'file',
			  formData: { 
				  
			  },success(res) {
				// console.log(res)
				const r = JSON.parse(res.data)
				// console.log(r)
				if(r.status == 200) {
					uni.hideLoading()
					_this.$api.msg("发布成功~")
					setTimeout(()=>{
						uni.showLoading({
							title:'预览中...'
						})
						_this.getGame(_this.curGameId)
					}, 1000)
				}else {
					_this.$api.msg("服务器正忙~")
				}
			  },fail() {
			  	uni.hideLoading()
				_this.$api.msg("服务器正忙~")
			  }
		
			});
			// console.log("123")
		},
		beforeRead(e) {
			// console.log(e)
		},
		fileIsEmpty() {
			if (this.picList.length == 0) {
				return true
			} else {
				return false
			}
		},
		previewOnClick(e) {
			if (e.index == 0) {
				this.picList = []
			}
			// console.log(e)
			// console.log('当前点击的是第' + e.index + '个按钮，点击内容是' + e.content.text)
		},
		previewChange(open) {
			// console.log('当前开启状态：' + open)
		},
		addTag(e) {
			console.log(e)
			if (this.tagList.length < 3 && e.allTags.length <= 3) {
				this.tagList = e.allTags
			} else {
				this.$api.msg("最多只能添加三个")
			}

		},
		delTag(e) {
			console.log(e)
			this.tagList = e.allTags
		},
		async getCategories() {
			const res = await this.$api.request({
				url: '/category'
			})
			// console.log(res)
			const _this = this
			res.data.forEach(function(a){
				_this.categories.push(a.category)
				_this.categoryIds.push(a.id)
			})
			// res.data.forEach(function(a){
			// 	_this.categories.push(a.category)
			// })
			// console.log(this.categories)
		}
	}
}
